.view-listing-div{
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.listing-description-col{
  margin: 10px 0px;
}
.listing-description-col p{
  padding: 12px;
  text-overflow: clip;
  margin-top: 10px;
}
.view-listing-div .listing_images{
  text-align: center;
 margin-left: 40%;
}
.view-listing-div .btn.btn-primary, h1{
  margin-top: 10px;
}

h1, h2{
  text-transform: uppercase;
}
@media only screen and (max-width: 600px) {

  .view-listing-div .listing_images {
      margin: 10px 0px;
      margin-left: 12%;
  }
  
